body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-image: url("https://image.freepik.com/free-vector/blue-abstract-acrylic-brush-stroke-textured-background_53876-86373.jpg");
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-size: cover;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.search-bar {
	border-radius: 25px;
	display: flex;
	flex-direction: row;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
}
.search-bar-lg {
	margin: 50px auto;
	border: 3px solid gray;
}
.search-bar-sm {
	border: 1px solid white;
}

.search-input {
	border: none;
	border-radius: 25px;
	transition: all 0.4s linear;
	background: none;
}
.search-input:focus {
	box-shadow: none;
	outline: none;
	background: none;
}
.search-input-lg {
	width: 350px;
}
.search-input-lg:focus {
	width: 600px;
}
.search-input-sm {
	width: 300px;
	color: white;
}
.search-input-sm:focus {
	width: 500px;
	color: white;
}

.btn-search {
	transition: all 0.2s;
}
.btn-search:hover {
	transform: scale(1.3);
}
.btn-search:focus {
	box-shadow: none;
	outline: none;
}
.btn-search-sm {
	color: white;
}
.btn-search-sm:hover {
	color: white;
}

.brand {
	background-image: linear-gradient(
		to right,
		#ff8177 0%,
		#ff867a 0%,
		#ff8c7f 21%,
		#f99185 52%,
		#cf556c 78%,
		#b12a5b 100%
	);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.brand-lg {
	line-height: 1.5;
	font-size: 60px;
}

.navbar {
	transition: all 1s ease-in-out;
}

button {
	border: none !important;
}

button:focus {
	box-shadow: none !important;
	outline: none !important;
}

a.card-text:hover {
	text-decoration: none;
}
a.card-text {
	transition: all 0.1s linear;
}

.col-xs-5ths,
.col-sm-5ths,
.col-md-5ths,
.col-lg-5ths,
.col-xl-5ths,
.col-xs-two5ths,
.col-sm-two5ths,
.col-md-two5ths,
.col-lg-two5ths,
.col-xl-two5ths,
.col-xs-three5ths,
.col-sm-three5ths,
.col-md-three5ths,
.col-lg-three5ths,
.col-xl-three5ths,
.col-xs-four5ths,
.col-sm-four5ths,
.col-md-four5ths,
.col-lg-four5ths,
.col-xl-four5ths,
.col-xs-five5ths,
.col-sm-five5ths,
.col-md-five5ths,
.col-lg-five5ths,
.col-xl-five5ths {
	position: relative;
	min-height: 1px;
	padding-right: 15px;
	padding-left: 15px;
}

@media (min-width: 576px) {
	.col-sm-5ths {
		width: 20%;
		float: left;
	}
	.col-sm-two5ths {
